import React from 'react'
import './segment.css'

import TopicCard from '../components/topiccard'

const cards = [
    {title: 'Human Language', desc: 'Ex) 텍스트 분석 , 음성발화 데이터셋'},
    {title: 'Health Care', desc: 'Ex) 흉부 이미지 데이터, 심전도 데이터'},
    {title: 'Automobile', desc: 'Ex) 특이 도로 환경 주행 데이터, 차로 위반 영상'},
    {title: 'Retail', desc: 'Ex) 상표 이미지 및 텍스트, 상품 이미지'},
    {title: 'Financial', desc: 'Ex) 금융업 특화 문서, 글로벌 부동산 가격 추이'},
    {title: 'Manfacture', desc: 'Ex) 드론 공장부지 촬영 영상, 불량 검수 데이터'},
    {title: 'Media/Entertainment', desc: 'Ex) 방송 콘텐츠 대본 요약, 트위터 텍스트 데이터'},
    {title: 'Agriculture', desc: 'Ex) 고품질 과수작물 통합 데이터, 지능형 스마트팜 통합 데이터'},
    {title: 'Environment/Safety', desc: 'Ex) 자연재해로 인한 생활시설 안전 데이터, 산림 수종 이미지'},
]

export default function Segment({nextPage}) {
    return <div className="segmentPage">
        {cards.map((card, idx) => <TopicCard title={card.title} desc={card.desc} nextPage={() => nextPage({fieldId: idx+1})} />)}
    </div>
}
